//  reader form 

.reader-form{
    width: 83vw;
    // border: 2px solid black;
    padding: 10px 10px;
    display: flex;
    // flex-direction: column;
    align-items: center;
    justify-content: center;
    
    

    .reader-form-background-color{
        height: 450px;
        width: 500px;
        // border: 2px solid black;
        background-image:url("../../../public/createuser.jpg");
        background-position: center;
        background-size:cover;
        background-repeat: no-repeat;
    }
    

    form{
     
        height: 450px;
        width: 450px;
        // border: 2px solid black;
        box-shadow: 0px 0px 20px gray;
        display: flex;
        flex-direction: column;
        // padding: 10px 10px;
        justify-content: center;
        // align-items: center;

        h3{
            // margin-top: 40px;
            margin-bottom: 30px;
            font-size: 2rem;
            margin-left: 55px;
            border-bottom: 2px solid #1677ff;
            width: fit-content;
        }

        .form-label{
            font-size: 1.6rem;
            margin-left: 55px;
        }
        input{
            height: 30px;
            width: 70%;
            margin-left: 50px;
        }
        .select-role{
            height: 30px;
            width: 70%;
            margin-left: 50px;
            border-radius: 5px;
            border: 1px solid lightgray;
        }
        span{
            height: 10px;
     
            font-size: 1.4rem;
            color:red;
            margin-left: 50px;
        }
        .reader-form-button{
            margin-left: 50px;
            width: 80px;
            span{
                color: aliceblue;
                margin-left: 0px;
            }
        }

       
    }
}

